import React from 'react';
import './Card.css';

class Card extends React.Component {
  render() {
    const { array } = this.props;
    return (
      <div className="cardContainer">
        <a target="noreferrer" href={array.link}>
          <img src={array.image} alt={array.name}/>
          <span>{array.name}</span>
        </a>
      </div>
    );
  }
}

export default Card;
